var _self;
var thead = [
	[
		{ text: '排名', slot: false, style: 'width: 70upx;' },
		{ text: '项目名称', slot: false, style: 'width: 153upx;' },
		{ text: 'PM10', slot: false, style: 'width: 85upx;' },
		{ text: 'TSP', slot: false, style: 'width: 60upx;' },
		{ text: '标站值', slot: false, style: 'width: 90upx;' },
		{ text: '标站名称', slot: false, style: 'width: 110upx;' },
		{ text: '查看', slot: false, style: 'width: 91upx;flex:1' }
	],
	[
		{ text: '排名', slot: false, style: 'width: 86upx;' },
		{ text: '区域', slot: false, style: 'width: 96upx;' },
		{ text: '项目名称', slot: false, style: 'width: 153upx;' },
		{ text: 'PM10', slot: false, style: 'width: 80upx;' },
		{ text: 'TSP', slot: false, style: 'width: 60upx;' },
		{ text: '标站值', slot: false, style: 'width: 90upx;' },
		{ text: '标站名称', slot: true, style: 'width: 91upx;flex:1' }
	],
	[
		{ text: '排名', slot: false, style: 'width: 86upx;' },
		{ text: '区域', slot: false, style: 'width: 96upx;' },
		{ text: '项目名称', slot: false, style: 'width: 153upx;' },
		{ text: 'PM10', slot: false, style: 'width: 90upx;' },
		{ text: '标站值', slot: false, style: 'width: 86upx;' },
		{ text: '星级', slot: true, style: 'width: 170upx;flex:1;' }
	]
];
export default {
	data() {
		return {
			time:"",
			tab_bar: ['项目排名', '区域排名', '星级排名'], //tab切换
			tab_cur: 0, //tab当前切换
			cWidth: '', //宽度
			cHeight: '', //高
			pixelRatio: 1, //缩放
			table: [
				[],
				[],
				[]
			],
			thead: [],
			tbody: [],
			show: false,
			type: '',
			start_time:"2019/12/14",
			end_time:"2019/12/14",
			pop_data:[
				{
					text:"一月",
					grade:3
				},
				{
					text:"二月",
					grade:4
				},
				{
					text:"三月",
					grade:5
				},
				{
					text:"四月",
					grade:6
				}
			]
		};
	},
	onLoad() {

		_self = this;
		_self.getDate()
		_self.thead = thead[_self.tab_cur];
	
	
	},

	methods: {
		onChangeTabBar(index) {
			_self.tab_cur = index;
			_self.thead = thead[_self.tab_cur];
			_self.tbody = _self.table[_self.tab_cur];
			_self.getDate()
		},
		onDetail(e) {
			let { col_index, col_item } = e;
			if(_self.tab_cur==0 && col_index==6){ 
				//项目排名详情
				_self.$Router.push({ name: 'analysis-detail', params: { }})
			}
			if(_self.tab_cur==2&&col_index==5){ //星级排名
				console.log(col_item);
				_self.togglePopup('center', 'popup')
			}
		},
		togglePopup(type, open) {
			this.type = type
			if (open === 'tip') {
				this.show = true
			} else {
				this.$refs[open].open()
			}
		},
		cancel(type) {
			if (type === 'tip') {
				this.show = false
				return
			}
			this.$refs[type].close()
		},
		change(e) {
			console.log(e.show)
		},
		getDate(){
			uni.showLoading({
				title:'努力加载中...'
			})
			console.log(_self.tab_cur);
			_self.$ajax({
				path: _self.$api.analysis,
				data:{
					type:_self.tab_cur+1
				},
				success(res){
					
					_self.time = res.data.times;
					
					uni.hideLoading();
				
					if(res.data.code == 2000 ){
						
						//先给大数组的相应数组赋值
		                _self.table[_self.tab_cur] =res.data.data
						
						// console.log(Date.parse(res.data.time.replace(/-/g, '/')) / 1000);
		

						
						//在进行模板渲染
					   	_self.tbody = _self.table[_self.tab_cur];
						
					
					}else{
					   uni.showToast({
					   	title: '请登录后再进行操作!',
					   	icon: "none"
					   });
						setTimeout({function(){
							uni.redirectTo({
								url:"/pages/user/login"
							})
						}},1000)
					}
				}
			})
		},
	}
};
